import { buildRequestInfo, buildResponse, HttpConfig, REQUEST_METHODS } from './helpers'

class Http {
  private static http: Http

  static create() {
    if (!Http.http) {
      return new Http()
    }

    return Http.http
  }

  private static async _request<R>(config: HttpConfig) {
    const { url, method, body, headers, responseType } = buildRequestInfo(config)

    const response = await fetch(url, { method, body, headers })

    return buildResponse<R>(response, responseType)
  }

  async request<R>(configOrUrl: string | HttpConfig, config: HttpConfig = {}) {
    if (typeof configOrUrl === 'string') {
      config.url = configOrUrl
    } else {
      config = configOrUrl
    }

    return Http._request<R>(config)
  }

  async get<R>(url: string, config: HttpConfig = {}) {
    return await this.request<R>(url, {
      ...config,
      method: REQUEST_METHODS.GET
    })
  }

  async post<R, D>(url: string, data: D, config: HttpConfig = {}) {
    return await this.request<R>(url, {
      ...config,
      method: REQUEST_METHODS.POST,
      data
    })
  }
}

const http = Http.create()

export default http
