import { PROXY_URI,type Region } from '@td-org/chat-widget-shared'

const BASE_URLS = {
  ['td-us-1']: import.meta.env.VITE_BASE_URL_TD_US_1,
  ['td-eu-1']: import.meta.env.VITE_BASE_URL_TD_EU_1,
  ['td-ca-1']: import.meta.env.VITE_BASE_URL_TD_CA_1,
  ['td-usfed-1']: import.meta.env.VITE_BASE_URL_TD_US_FED_1
}

const BASE_URLS_PROXY = {
  ['td-us-1']: PROXY_URI.tdGatewayUs,
  ['td-eu-1']: PROXY_URI.tdGatewayEu,
  ['td-ca-1']: PROXY_URI.tdGatewayCa,
  ['td-usfed-1']: PROXY_URI.tdGatewayUsFed
}

export function getBaseUrl(region: Region, proxyHost?: string) {
  if (proxyHost) {
    return `//${proxyHost}${BASE_URLS_PROXY[region]}`
  }
  return BASE_URLS[region]
}
