import { downloadByBlobs } from '../utils/download'

import { RESPONSE_TYPES } from './helpers'
import http from './http'

export async function downloadTranscript({
  interactionId = '',
  token = '',
  timezone = '',
  fileName = 'unknown.txt'
}: {
  interactionId: string
  token: string
  fileName: string
  timezone: string
}) {
  if (!interactionId) {
    throw new Error('Interaction ID is required')
  }
  if (!token) {
    throw new Error('Token is required')
  }

  const url = `/cds/interactions/${interactionId}/messages/transcript`

  const source = await http.get<BlobPart>(url, {
    params: { timezone },
    token: token,
    responseType: RESPONSE_TYPES.BLOB
  })

  return downloadByBlobs([source], fileName)
}
