import { type AppConfig, type AppProps } from '@td-org/chat-widget-shared'
import { RpcConstants } from '@td-org/chat-widget-shared'
import RPC from '@td-org/digital-postmessage-rpc'

import { fetchCredentials, fetchTdAccessToken, type TwilioTokenRequest } from './api/getAccessToken'
import { getTouchpoint } from './api/getTouchpoint'
import { downloadTranscript } from './api/getTranscript'
import { Optional } from './utils/Optional.ts'
import { safeCall } from './utils/safeCall.ts'
import storage from './utils/storage'
import { injectStyles, listenerScreenChange, updateThemes } from './styles'
import { WidgetFrame, WidgetTrigger, WidgetWrapper } from './WidgetElement'

type Callback = () => void

export default function TalkdeskChatSDK(injector: string, props: AppProps) {
  const root = document.getElementById(injector)
  if (!root) {
    throw new Error(`The element not exist, element id = "#${injector}"`)
  }
  const touchpointId = (props.touchpointId || props.flowId) as string
  if (!touchpointId.trim()) {
    throw new Error('The td chat jssdk touchpointId or flowId no exist')
  }
  if (!props.region) {
    throw new Error('The td chat jssdk region not exist')
  }
  storage.setAppProps({
    ...props,
    touchpointId
  })

  const cleanups: Callback[] = []
  let cachedTouchpointPromise: ReturnType<typeof getTouchpoint> | null = null
  let initialized = false
  let frameReadied = false
  let initResolve: ((value: boolean | PromiseLike<boolean>) => void) | null = null
  let rpc: RPC | null = null
  let unListenerScreenChange: (() => void) | null = null

  const onWidgetOpen = Optional.empty<Callback>()
  const onWidgetClose = Optional.empty<Callback>()
  const onConversationStart = Optional.empty<Callback>()

  const widgetWrapper = WidgetWrapper({ root })
  const widgetTrigger = WidgetTrigger(widgetWrapper.element, () => {
    open()
  })
  const widgetFrame = WidgetFrame(widgetWrapper.element, (window) => {
    rpc = new RPC({
      name: 'SDK',
      sender: window,
      topic: RpcConstants.Topic,
      debug: import.meta.env.MODE === 'development'
    })

    rpc.register(RpcConstants.Methods.FrameReady, () => {
      if (!frameReadied) {
        frameReadied = true
        initResolve?.(true)
        widgetTrigger.open()
      }
    })

    rpc.register(RpcConstants.Methods.FrameResize, (payload) => {
      widgetFrame.resize(payload)
    })

    rpc.register(RpcConstants.Methods.Maximize, () => {
      open()
    })

    rpc.register(RpcConstants.Methods.Minimize, () => {
      close()
    })

    rpc.register(RpcConstants.Methods.GetAllConfig, () => {
      return storage.getAllConfig()
    })

    rpc.register(RpcConstants.Methods.GetCredentials, (payload: Omit<TwilioTokenRequest, 'page_url'>) =>
      fetchCredentials({ ...payload, app_id: touchpointId })
    )

    rpc.register(RpcConstants.Methods.ConversationStart, () => {
      safeCall(onConversationStart.current, (e: unknown) => {
        console.error('An error occurred in "onConversationStart" callback.', e)
      })
    })

    rpc.register(RpcConstants.Methods.SetBadge, (count: number) => {
      widgetTrigger.setBadge(count)
    })

    rpc.register(RpcConstants.Methods.GetTdAccessToken, () => fetchTdAccessToken(touchpointId))

    rpc.register(
      RpcConstants.Methods.DownloadTranscript,
      async (payload: { interactionId: string; token: string; fileName: string; timezone: string }) => {
        await downloadTranscript(payload)
        return true
      }
    )
  })

  const init = async ({
    enableResponsiveLayout = true,
    enableUserInput = true,
    enablePointMoveTriggerButton = false,
    ...appConfig
  }: AppConfig = {}) => {
    if (!cachedTouchpointPromise) {
      cachedTouchpointPromise = getTouchpoint(touchpointId)
    }
    const touchpoint = await cachedTouchpointPromise
    if (!touchpoint?.active) {
      return false
    }

    const configs = storage.setAppConfig({
      ...appConfig,
      enableResponsiveLayout,
      enableUserInput,
      enablePointMoveTriggerButton,
      languageCode: appConfig?.languageCode || props?.languageCode
    })
    storage.setTouchpoint(touchpoint)

    if (!initialized) {
      initialized = true

      const themes = updateThemes(widgetWrapper.element, configs?.styles, touchpoint)
      unListenerScreenChange = listenerScreenChange(widgetWrapper.element)
      cleanups.push(injectStyles(widgetWrapper.element))
      widgetFrame.update({ enableResponsiveLayout })
      widgetTrigger.update({ enablePointMoveTriggerButton, themes })

      return new Promise<boolean>((resolve) => {
        initResolve = resolve
        widgetFrame.initInBackground()
      })
    } else {
      await rpc?.trigger(RpcConstants.Methods.UpdateAppConfigs, { appConfig: configs })
      return true
    }
  }

  const open = () => {
    widgetTrigger.close()
    widgetFrame.open()
    rpc?.trigger(RpcConstants.Methods.FrameOpened)
    safeCall(onWidgetOpen.current, (e: unknown) => {
      console.error('An error occurred in "onOpenWebchat" callback.', e)
    })
  }

  const close = () => {
    widgetFrame.close()
    widgetTrigger.open()
    safeCall(onWidgetClose.current, (e: unknown) => {
      console.error('An error occurred in "onCloseWebchat" callback.', e)
    })
  }

  const destroy = () => {
    widgetTrigger.destroy()
    widgetFrame.destroy()
    widgetWrapper.destroy()
    cleanups.forEach((cleanup) => cleanup())
    cleanups.length = 0
    rpc?.destroy()
    rpc = null
    onWidgetClose.current = null
    onWidgetOpen.current = null
    onConversationStart.current = null
    storage.clearAll()
    cachedTouchpointPromise = null
    initialized = false
    frameReadied = false
    initResolve = null
    unListenerScreenChange?.()
    unListenerScreenChange = null
  }

  const setContextParam = (data: Record<string, string>) => {
    storage.setContextParam(data)
    rpc?.trigger(RpcConstants.Methods.UpdateContext, data)
  }

  return {
    open,
    close,
    init,
    destroy,
    setContextParam,
    set onOpenWebchat(callback: Callback) {
      onWidgetOpen.current = callback
    },
    set onCloseWebchat(callback: Callback) {
      onWidgetClose.current = callback
    },
    set onConversationStart(callback: Callback) {
      onConversationStart.current = callback
    }
  }
}
