import { type AppProps } from '@td-org/chat-widget-shared'

import type { PreviewSDK } from './entryPreview.ts'
import TalkdeskChatSDK from './TalkdeskChatSDK'

export type SDK = ReturnType<typeof TalkdeskChatSDK>
export type { PreviewSDK }

function entryPoint(injector: string, props: AppProps): SDK {
  return TalkdeskChatSDK(injector, props)
}

export default entryPoint
