import type { Region } from '@td-org/chat-widget-shared'

import storage from '../utils/storage'

import { getBaseUrl } from './getApiUrl'

export enum RESPONSE_TYPES {
  TEXT = 'TEXT',
  JSON = 'JSON',
  BLOB = 'BLOB',
  ARRAY_BUFFER = 'ARRAY_BUFFER'
}

export enum REQUEST_METHODS {
  GET = 'GET',
  POST = 'POST'
}

export type ResponseType = keyof typeof RESPONSE_TYPES
export type RequestMethod = keyof typeof REQUEST_METHODS
export type HttpConfig = {
  url?: string
  method?: RequestMethod
  params?: Record<string, string | number | boolean>
  data?: unknown
  token?: string
  responseType?: ResponseType
}
export type RequestInfo = {
  url: string
  method: RequestMethod
  body?: string
  headers: Record<string, string>
  responseType: ResponseType
}

export const HEADERS: Record<string, string> = {
  'Content-Type': 'application/json'
}

class HttpError extends Error {
  code: number
  response: unknown

  constructor(message: string, attr: { code: number; response: unknown }) {
    super(message)

    this.code = attr.code
    this.response = attr.response
  }
}

function encode(str: string | number | boolean): string {
  const charMap: Record<string, string> = {
    '!': '%21',
    "'": '%27',
    '(': '%28',
    ')': '%29',
    '~': '%7E',
    '%20': '+',
    '%00': '\x00'
  }
  return encodeURIComponent(str).replace(/[!'()~]|%20|%00/g, (match) => charMap[match])
}

function parseParams(params: Record<string, string | number | boolean>): string {
  return Object.entries(params)
    .reduce<string[]>((arr, [k, v]) => {
      arr.push(`${encode(k)}=${encode(v)}`)

      return arr
    }, [])
    .join('&')
}

function validateStatus(status: number) {
  return status >= 200 && status < 300
}

function buildFullPath(url: string): string {
  const { appProps } = storage.getAllConfig()
  const baseURL = getBaseUrl(appProps?.region as Region, appProps?.proxyHost)

  return url ? baseURL + url : baseURL
}

function buildUrl({ url, params }: HttpConfig): string {
  let fullPath = buildFullPath(url!)

  if (params) {
    const parsedParams = parseParams(params)

    const hashIndex = fullPath.indexOf('#')

    if (hashIndex !== -1) {
      fullPath = fullPath.slice(0, hashIndex)
    }

    fullPath += (fullPath.indexOf('?') === -1 ? '?' : '&') + parsedParams
  }

  return fullPath
}

function buildBody({ data }: HttpConfig): string | undefined {
  return data ? JSON.stringify(data) : undefined
}

function buildHeaders({ token }: HttpConfig) {
  const headers: Record<string, string> = { ...HEADERS }

  if (token) {
    headers['Authorization'] = `Bearer ${token}`
  }

  return headers
}

export function buildRequestInfo(config: HttpConfig = {}): RequestInfo {
  const responseType = config.responseType || RESPONSE_TYPES.JSON

  const method = config.method || REQUEST_METHODS.GET

  const url = buildUrl(config)

  const body = buildBody(config)

  const headers = buildHeaders(config)

  return {
    url,
    method,
    body,
    headers,
    responseType
  }
}

export async function buildResponse<T>(
  response: Response,
  responseType: ResponseType = RESPONSE_TYPES.JSON
): Promise<T> {
  let data

  switch (responseType) {
    case RESPONSE_TYPES.TEXT:
      data = await response.text()
      break
    case RESPONSE_TYPES.JSON:
      data = await response.json()
      break
    case RESPONSE_TYPES.BLOB:
      data = await response.blob()
      break
    case RESPONSE_TYPES.ARRAY_BUFFER:
      data = await response.arrayBuffer()
      break
    default:
      break
  }

  if (response.ok && validateStatus(response.status)) {
    return data
  }

  return Promise.reject(
    new HttpError(`Failed to http status error ${response.status}`, {
      code: response.status,
      response
    })
  )
}
