import { promiseRetry, type Touchpoint } from '@td-org/chat-widget-shared'

import http from './http'

export async function getTouchpoint(touchpointValue: string): Promise<Touchpoint | null> {
  try {
    return await promiseRetry(
      async () => {
        const response = await http.get<Touchpoint>('/2019-11/pas/livechats', {
          params: {
            touchpoint_value: touchpointValue
          }
        })

        return {
          active: response.active,
          config: response.config,
          account_id: response.account_id,
          release_version: response.release_version,
          enable_split_twilio_bootstrap: !!response?.enable_split_twilio_bootstrap,
          feedback_flow_id: response?.feedback_flow_id || ''
        }
      },
      { defaultRetry: 2 }
    )
  } catch (error) {
    console.error('Failed to fetch td chat touchpoint', error)
    return null
  }
}
