import { GET_CREDENTIALS_TYPE } from '@td-org/chat-widget-shared'

import http from './http'

type TDTokenRequest = { app_id: string }

type TDTokenResponse = { td_access_token: string }

type TwilioTokenResponse = {
  access_token: string
  conversation_sid: string
  region?: string
}

export type TwilioTokenRequest = {
  app_id: string
  identity: string
  page_url: string
  custom_fields?: Record<string, string>
  bootstrap_type?: (typeof GET_CREDENTIALS_TYPE)[keyof typeof GET_CREDENTIALS_TYPE]
}

export async function fetchTdAccessToken(touchpointId: string) {
  const url = '/pas/livechat-auth'
  const { td_access_token } = await http.post<TDTokenResponse, TDTokenRequest>(url, { app_id: touchpointId })
  return td_access_token
}

export async function fetchCredentials(payload: Omit<TwilioTokenRequest, 'page_url'>) {
  const url = '/2019-11/pas/livechats'

  const { access_token, conversation_sid, region } = await http.post<TwilioTokenResponse, TwilioTokenRequest>(url, {
    ...payload,
    page_url: window.location.href
  })

  return {
    accessToken: access_token,
    roomId: conversation_sid,
    region: region
  }
}
